<template>
  <div class="d-flex justify-center flex-wrap">
    <div style="flex: 0 0 100%">
      <v-chip v-if="role=='teacher'" label :ripple="false">{{ className}}</v-chip>
      <span class="ml-4"></span>
      <v-checkbox
        class="d-inline-block float-right"
        :label="''"
        @change="onChangeSelectAll($event)"
      ></v-checkbox>
    </div>
    <div class="mt-6 d-flex flex-wrap justify-space-between" style="flex: 0 0 100%">
      <v-card class="my-2" v-for="(classmate,index) in classmates" :key="index">
        <v-avatar
          @click="changeStatus(classmate)"
          :color="classmate.selected?'primary':'grey'"
          :size="90"
        ></v-avatar>
        <div>{{ classmate.name }}</div>
      </v-card>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      className: "",
      classmates:
        this.role == "teacher"
          ? [
              { name: "1", selected: false },
              { name: "2", selected: false },
              { name: "3", selected: false },
              { name: "4", selected: false },
              { name: "5", selected: false },
              { name: "6", selected: false },
              { name: "7", selected: false },
              { name: "8", selected: false },
              { name: "9", selected: false },
            ]
          : [
              { name: "1", selected: false },
              { name: "2", selected: false },
              { name: "3", selected: false },
              { name: "4", selected: false },
            ],
    };
  },
  methods: {
    changeStatus(mate) {
      mate.selected = !mate.selected;
    },
    onChangeSelectAll($event) {
      this.classmates.forEach((c) => (c.selected = $event));
    },
  },
  computed: {
    role() {
      return this.$store.getters.role;
    },
  },
};
</script>

